.btn-nav {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  /* border: none; */
  border: solid 2px #fff;
  cursor: pointer;
  color: #fff;
  transition: 0.1s;
}

.btn--primary {
  background-color: var(--primary);
  color: #fff;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 8px 20px;
  font-size: 18px;
}

.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn--large:hover,
.btn--medium:hover {
  /* transition: all 0.3s ease-out; */
  background: #fff;
  border: solid 2px #fff;
  color: orange;
  transition: 20ms;
}

